import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
  IconButton,
  Button,
  Box,
  Typography,
  Tooltip,
  TableFooter,
  TablePagination,
} from '@mui/material';
import {
  Add as AddIcon,
  Delete as DeleteIcon,
  Edit as EditIcon,
  Save as SaveIcon,
  Cancel as CancelIcon,
  DragIndicator as DragHandleIcon,
} from '@mui/icons-material';
import { EffortColumn, EstimationRow } from '../../types/quoteTemplate';

// Cc ct c nh khng th xa
const FIXED_COLUMNS: Omit<EffortColumn, 'id' | 'key' | 'order'>[] = [
  { name: 'Feature', isFixed: true },
  { name: 'Detail', isFixed: true },
  { name: 'Description', isFixed: true },
  { name: 'Notes', isFixed: true },
  { name: 'IT Req', isFixed: true, isEffort: true },
  { name: 'UX/UI', isFixed: true, isEffort: true },
];

interface FixedPriceTableProps {
  rows: EstimationRow[];
  effortColumns: EffortColumn[];
  onRowsChange: (rows: EstimationRow[]) => void;
  onColumnsChange: (columns: EffortColumn[]) => void;
}

const FixedPriceTable: React.FC<FixedPriceTableProps> = ({
  rows = [],
  effortColumns = [],
  onRowsChange,
  onColumnsChange,
}) => {
  const { t } = useTranslation();
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [newColumnName, setNewColumnName] = useState('');
  const [editingColumn, setEditingColumn] = useState<string | null>(null);
  const [editColumnName, setEditColumnName] = useState('');

  // X l thay i trang
  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  // X l thay i s dng mi trang
  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  // Thm ct effort mi
  const handleAddColumn = () => {
    if (!newColumnName.trim()) return;
    
    // t th t ca ct mi cao hn cc ct c nh nhng thp hn ct hnh ng
    // Ct hnh ng s c th t cao nht (1000)
    const newColumn: EffortColumn = {
      id: `col-${Date.now()}`,
      name: newColumnName.trim(),
      key: `effort-${Date.now()}`,
      order: 900, // Th t cao  m bo nm sau cc ct c nh nhng trc ct hnh ng
      isFixed: false,
      color: '#ffffff' // Mu mc nh
    };

    onColumnsChange([...effortColumns, newColumn]);
    setNewColumnName('');
  };

  // Bt u chnh sa tn ct
  const startEditingColumn = (columnId: string, currentName: string) => {
    setEditingColumn(columnId);
    setEditColumnName(currentName);
  };

  // Lu tn ct  chnh sa
  const saveColumnName = (columnId: string) => {
    const updatedColumns = effortColumns.map(col => 
      col.id === columnId ? { ...col, name: editColumnName } : col
    );
    onColumnsChange(updatedColumns);
    setEditingColumn(null);
  };

  // Xa ct
  const handleDeleteColumn = (columnId: string) => {
    const columnToDelete = effortColumns.find(col => col.id === columnId);
    if (!columnToDelete?.isFixed) {
      const updatedColumns = effortColumns.filter(col => col.id !== columnId);
      onColumnsChange(updatedColumns);
      
      // Cp nht d liu hng  xa ct effort tng ng
      const updatedRows = rows.map(row => {
        const { [columnToDelete?.key || '']: _, ...restEfforts } = row.efforts || {};
        return { ...row, efforts: restEfforts };
      });
      onRowsChange(updatedRows);
    }
  };

  // Cp nht gi tr 
  const handleCellChange = (
    rowId: string,
    field: string,
    value: string | number
  ) => {
    const updatedRows = rows.map(row => {
      if (row.id === rowId) {
        if (field.startsWith('effort-')) {
          return {
            ...row,
            efforts: {
              ...row.efforts,
              [field]: Number(value) || 0,
            },
          };
        }
        return { ...row, [field]: value };
      }
      return row;
    });
    onRowsChange(updatedRows);
  };

  // Thm hng mi
  const handleAddRow = () => {
    const newRow: EstimationRow = {
      id: `row-${Date.now()}`,
      feature: '',
      detail: '',
      description: '',
      notes: '',
      itReq: '',
      uxUi: '',
      efforts: {},
    };
    onRowsChange([...rows, newRow]);
  };

  // Xa hng
  const handleDeleteRow = (rowId: string) => {
    onRowsChange(rows.filter(row => row.id !== rowId));
  };

  // Tnh tng cho tng ct effort
  const calculateColumnTotal = (columnKey: string) => {
    return rows.reduce((sum, row) => {
      return sum + (Number(row.efforts?.[columnKey]) || 0);
    }, 0);
  };

  // Ly tt c cc ct (c nh + ng)
  const fixedColumnsWithIds = FIXED_COLUMNS.map((col, index) => ({
    ...col,
    id: `fixed-${index}`,
    key: col.name.toLowerCase().replace(/\s+/g, '-'),
    order: index, // Cc ct c nh c th t thp (0-5)
  }));
  
  // Ch ly cc ct effort khng trng vi cc ct c nh
  const nonDuplicateEffortColumns = effortColumns.filter(effortCol => 
    !fixedColumnsWithIds.some(fixedCol => fixedCol.key === effortCol.key)
  );
  
  // Thm ct Subtotal (days) vi th t cao
  const subtotalColumn = {
    id: 'subtotal-column',
    name: t('pages.quotations.estimation.fixed.subtotalDays'),
    key: 'subtotal',
    order: 950, // Th t cao  m bo nm sau cc ct effort nhng trc ct hnh ng
    isFixed: true,
  };
  
  // Sp xp cc ct theo th t
  const allColumns = [
    ...fixedColumnsWithIds,
    ...nonDuplicateEffortColumns,
    subtotalColumn, // Thm ct subtotal
  ].sort((a, b) => a.order - b.order);

  return (
    <Box>
      {/* Thanh cng c */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
        <Box>
          <Button
            variant="contained"
            color="primary"
            startIcon={<AddIcon />}
            onClick={handleAddRow}
            size="small"
            sx={{ mr: 1 }}
          >
            {t('pages.quotations.estimation.fixed.addRow')}
          </Button>
        </Box>
        
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <TextField
            size="small"
            placeholder={t('pages.quotations.estimation.fixed.newColumnPlaceholder')}
            value={newColumnName}
            onChange={(e) => setNewColumnName(e.target.value)}
            sx={{ mr: 1, minWidth: 150 }}
          />
          <Button
            variant="outlined"
            startIcon={<AddIcon />}
            onClick={handleAddColumn}
            size="small"
            disabled={!newColumnName.trim()}
          >
            {t('pages.quotations.estimation.fixed.addColumn')}
          </Button>
        </Box>
      </Box>

      {/* Bng d liu */}
      <TableContainer component={Paper} sx={{ maxHeight: 600, overflow: 'auto' }}>
        <Table stickyHeader size="small">
          <TableHead>
            <TableRow>
              <TableCell width={50}>#</TableCell>
              {allColumns.map((column) => (
                <TableCell key={column.id}>
                  <Box sx={{ display: 'flex', alignItems: 'center' }}>
                    <DragHandleIcon 
                      sx={{ 
                        mr: 1, 
                        cursor: 'move',
                        opacity: column.isFixed ? 0.3 : 1,
                        '&:hover': {
                          opacity: 1,
                        },
                      }} 
                    />
                    
                    {editingColumn === column.id ? (
                      <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <TextField
                          value={editColumnName}
                          onChange={(e) => setEditColumnName(e.target.value)}
                          size="small"
                          sx={{ mr: 1 }}
                        />
                        <IconButton 
                          size="small" 
                          onClick={() => saveColumnName(column.id)}
                          color="primary"
                        >
                          <SaveIcon fontSize="small" />
                        </IconButton>
                        <IconButton 
                          size="small" 
                          onClick={() => setEditingColumn(null)}
                          color="error"
                        >
                          <CancelIcon fontSize="small" />
                        </IconButton>
                      </Box>
                    ) : (
                      <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <Typography variant="subtitle2">
                          {(() => {
                            // Map fixed column keys to i18n labels; fallback to column.name for dynamic columns
                            switch (column.key) {
                              case 'feature':
                                return t('pages.quotations.estimation.table.feature');
                              case 'detail':
                                return t('pages.quotations.estimation.table.detail');
                              case 'description':
                                return t('pages.quotations.estimation.table.description');
                              case 'notes':
                                return t('pages.quotations.estimation.table.note');
                              case 'it-req':
                                return t('pages.quotations.estimation.fixed.columns.itReq');
                              case 'ux/ui':
                                return t('pages.quotations.estimation.fixed.columns.uxUi');
                              case 'subtotal':
                                return t('pages.quotations.estimation.fixed.subtotalDays');
                              default:
                                return column.name;
                            }
                          })()}
                        </Typography>
                        {!column.isFixed && (
                          <Box sx={{ ml: 1 }}>
                            <IconButton 
                              size="small" 
                              onClick={() => startEditingColumn(column.id, column.name)}
                              sx={{ p: 0.5 }}
                            >
                              <EditIcon fontSize="small" />
                            </IconButton>
                            <IconButton 
                              size="small" 
                              onClick={() => handleDeleteColumn(column.id)}
                              sx={{ p: 0.5 }}
                              color="error"
                            >
                              <DeleteIcon fontSize="small" />
                            </IconButton>
                          </Box>
                        )}
                      </Box>
                    )}
                  </Box>
                </TableCell>
              ))}
              {/* Action column always at the end */}
              <TableCell width={80}>{t('pages.quotations.tables.actions')}</TableCell>
            </TableRow>
          </TableHead>
          
          <TableBody>
            {rows
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((row, rowIndex) => (
                <TableRow key={row.id} hover>
                  <TableCell>{page * rowsPerPage + rowIndex + 1}</TableCell>
                  
                  {allColumns.map((column) => {
                    // Kim tra nu column key l ca cc ct effort
                    const isEffortColumn = column.key === 'it-req' || column.key === 'ux/ui' || 
                                         column.key.startsWith('effort-') || 
                                         (column as EffortColumn).isEffort === true;
                    
                    // Log ra  kim tra key ca cc ct
                    console.log('Column key:', column.key, 'isEffort:', isEffortColumn);
                    
                    // Kim tra nu l ct subtotal
                    const isSubtotalColumn = column.key === 'subtotal';
                    
                    return (
                      <TableCell key={`${row.id}-${column.key}`}>
                        {isEffortColumn ? (
                          <TextField
                            type="number"
                            value={column.key === 'it-req' ? row.itReq || '' : 
                                  column.key === 'ux/ui' ? row.uxUi || '' : 
                                  row.efforts?.[column.key] || ''}
                            onChange={(e) => {
                              const value = e.target.value;
                              // X l c bit cho cc ct IT Req v UX/UI
                              if (column.key === 'it-req') {
                                handleCellChange(row.id, 'itReq', value);
                              } else if (column.key === 'ux/ui') {
                                handleCellChange(row.id, 'uxUi', value);
                              } else {
                                handleCellChange(row.id, column.key, value);
                              }
                            }}
                            size="small"
                            fullWidth
                            inputProps={{ min: 0, step: 0.5 }}
                          />
                        ) : isSubtotalColumn ? (
                          // Hin th tng ngy cng cho mi dng
                          <Typography variant="body2" fontWeight="bold">
                            {Object.keys(row.efforts || {}).reduce((sum, key) => sum + (Number(row.efforts?.[key]) || 0), 0) + 
                             (Number(row.itReq) || 0) + (Number(row.uxUi) || 0)}
                          </Typography>
                        ) : (
                          <TextField
                            value={row[column.key as keyof EstimationRow] || ''}
                            onChange={(e) => 
                              handleCellChange(row.id, column.key, e.target.value)
                            }
                            size="small"
                            fullWidth
                            multiline
                            maxRows={4}
                          />
                        )}
                      </TableCell>
                    );
                  })}
                  
                  <TableCell>
                    <Tooltip title={t('pages.quotations.estimation.fixed.deleteRow')}>
                      <IconButton 
                        size="small" 
                        color="error"
                        onClick={() => handleDeleteRow(row.id)}
                      >
                        <DeleteIcon fontSize="small" />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
          
          {/* Dng tng cng */}
          <TableFooter>
            <TableRow>
              <TableCell colSpan={FIXED_COLUMNS.length + 1} align="right">
                <strong>{t('pages.quotations.estimation.fixed.total')}</strong>
              </TableCell>
              {effortColumns.map((column) => (
                <TableCell key={`total-${column.key}`}>
                  <strong>{calculateColumnTotal(column.key)}</strong>
                </TableCell>
              ))}
              {/* Ct Subtotal tng */}
              <TableCell>
                <strong>
                  {rows.reduce((sum, row) => {
                    const rowTotal = Object.keys(row.efforts || {}).reduce(
                      (rowSum, key) => rowSum + (Number(row.efforts?.[key]) || 0), 0
                    ) + (Number(row.itReq) || 0) + (Number(row.uxUi) || 0);
                    return sum + rowTotal;
                  }, 0)}
                </strong>
              </TableCell>
              <TableCell />
            </TableRow>
            {/* Thm dng Tng cng - Theo giai on (ngy cng) */}
            <TableRow sx={{ backgroundColor: '#f5f5f5' }}>
              <TableCell colSpan={FIXED_COLUMNS.length + effortColumns.length + 1} align="right">
                <strong>{t('pages.quotations.estimation.fixed.totalByPhase')}</strong>
              </TableCell>
              <TableCell>
                <strong>
                  {rows.reduce((sum, row) => {
                    const rowTotal = Object.keys(row.efforts || {}).reduce(
                      (rowSum, key) => rowSum + (Number(row.efforts?.[key]) || 0), 0
                    ) + (Number(row.itReq) || 0) + (Number(row.uxUi) || 0);
                    return sum + rowTotal;
                  }, 0)}
                </strong>
              </TableCell>
              <TableCell />
            </TableRow>
          </TableFooter>
        </Table>
      </TableContainer>
      
      {/* Pagination */}
      <TablePagination
        rowsPerPageOptions={[5, 10, 25, 50]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
        labelRowsPerPage={t('pages.quotations.pagination.rowsPerPage')}
        labelDisplayedRows={({ from, to, count }) => 
          t('pages.quotations.pagination.displayedRows', { from, to, count })
        }
      />
    </Box>
  );
};

export default FixedPriceTable;
