const generateFakeQuotations = (count: number): Quotation[] => {
  return Array.from({ length: count }, (_, i) => ({
    id: i + 1,
    name: `Bo gi ${i + 1} - ${['Website bn hng', 'H thng kho', 'App di ng', 'Phn mm k ton'][i % 4]}`,
    customer: fakeCustomers[Math.floor(Math.random() * fakeCustomers.length)],
    dealId: i % 4 + 1,
    dealName: fakeDeals[i % 4].name,
    requirementId: i % 4 + 1,
    requirementName: fakeRequirements[i % 4].name,
    status: ['draft', 'sent', 'approved', 'rejected'][Math.floor(Math.random() * 4)] as QuotationStatus,
    createdDate: new Date(Date.now() - Math.floor(Math.random() * 90) * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
    note: '',
    items: [],
    totalEffort: Math.floor(Math.random() * 100) + 20,
    totalAmount: (Math.floor(Math.random() * 100) + 20) * 500000,
  }));
};

const statusOptions = [
  { value: 'draft', label: 'Draft', color: 'default' },
  { value: 'sent', label: ' gi', color: 'info' },
  { value: 'approved', label: ' duyt', color: 'success' },
  { value: 'rejected', label: 'T chi', color: 'error' },
];

const QuotationManagement = () => {
  const { t } = useTranslation();
  // Tab state
  const [currentTab, setCurrentTab] = useState<QuoteTemplateTab>('quotes');
  
  // State
  const [searchTerm, setSearchTerm] = useState('');
  const [customerFilter, setCustomerFilter] = useState<string | null>(null);
  const [statusFilter, setStatusFilter] = useState<string>('all');
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [loading, setLoading] = useState(false);
  
  // Template states
  const [templates, setTemplates] = useState<QuoteTemplate[]>([]);
  const [uploadDialogOpen, setUploadDialogOpen] = useState(false);
  const [selectedTemplate, setSelectedTemplate] = useState<QuoteTemplate | null>(null);
  
  // Quotations state
  const [quotations, setQuotations] = useState<Quotation[]>(generateFakeQuotations(25));
  
  // Create dialog state
  const [createDialogOpen, setCreateDialogOpen] = useState(false);
  
  // Detail dialog state
  const [detailDialogOpen, setDetailDialogOpen] = useState(false);
  const [selectedQuotation, setSelectedQuotation] = useState<Quotation | null>(null);
  
  // Preview dialog state
  const [previewDialogOpen, setPreviewDialogOpen] = useState(false);
  
  // Email dialog state
  const [emailDialogOpen, setEmailDialogOpen] = useState(false);
  const [emailQuotation, setEmailQuotation] = useState<Quotation | null>(null);
  
  // Load fake data on init
  useEffect(() => {
    setLoading(true);
    // Load quotations
    setQuotations(generateFakeQuotations(25));
    
    // Load templates
    const mockTemplates: QuoteTemplate[] = [
      {
        id: '1',
        name: 'Mu bo gi d n phn mm',
        description: 'Template bo gi d n phn mm tiu chun',
        type: 'customer',
        status: 'active',
        createdAt: '2025-06-30T10:00:00Z',
        updatedAt: '2025-06-30T10:00:00Z',
        createdBy: 'Nguyn Vn A',
        placeholderCount: 8,
        isDefault: true,
        extractedFields: []
      },
      {
        id: '2',
        name: 'Mu bo gi dch v IT',
        description: 'Template bo gi dch v IT outsourcing',
        type: 'internal',
        status: 'active',
        createdAt: '2025-06-29T15:30:00Z',
        updatedAt: '2025-06-29T15:30:00Z',
        createdBy: 'Trn Th B',
        placeholderCount: 5,
        isDefault: false,
        extractedFields: []
      }
    ];
    
    setTemplates(mockTemplates);
    setLoading(false);
  }, []);

  // Handlers
  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(event.target.value);
    setPage(0);
  };
  
  // Handle tab change
  const handleTabChange = (_event: React.SyntheticEvent, newValue: QuoteTemplateTab) => {
    setCurrentTab(newValue);
    setPage(0);
  };

  // Template handlers
  const handleOpenUploadDialog = () => {
    setUploadDialogOpen(true);
  };

  const handleCloseUploadDialog = () => {
    setUploadDialogOpen(false);
  };
  
  const handleUploadTemplate = async (values: UploadQuoteTemplateFormValues): Promise<void> => {
    try {
      setLoading(true);
      // TODO: Replace with actual API call
      console.log('Uploading template:', values);
      
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // Add new template to the list
      const newTemplate: QuoteTemplate = {
        id: `template-${Date.now()}`,
        name: values.name,
        description: values.description,
        type: values.type,
        status: 'active',
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
        createdBy: 'Ngi dng hin ti',
        placeholderCount: Math.floor(Math.random() * 10) + 3, // Random number of placeholders
        isDefault: false,
        extractedFields: []
      };
      
      setTemplates(prev => [newTemplate, ...prev]);
      setUploadDialogOpen(false);
      
    } catch (error) {
      console.error('Error uploading template:', error);
    } finally {
      setLoading(false);
    }
  };
  
  const handleViewTemplate = (templateId: string) => {
    const template = templates.find(t => t.id === templateId);
    if (template) {
      setSelectedTemplate(template);
      // Here you would implement the template viewing functionality
      console.log('Viewing template:', template);
    }
  };
  
  const handleDownloadTemplate = (templateId: string) => {
    const template = templates.find(t => t.id === templateId);
    if (template) {
      // Here you would implement the template download functionality
      console.log('Downloading template:', template);
    }
  };
  
  const handleDeleteTemplate = (templateId: string) => {
    // Here you would implement the template deletion functionality
    setTemplates(prev => prev.filter(t => t.id !== templateId));
    console.log('Deleted template with ID:', templateId);
  };
  
  const handleCustomerFilterChange = (_: any, newValue: string | null) => {
    setCustomerFilter(newValue);
    setPage(0);
  };

  const handleStatusFilterChange = (event: SelectChangeEvent<string>) => {
    setStatusFilter(event.target.value);
    setPage(0);
  };

  const handlePageChange = (_: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleRowsPerPageChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleViewQuotationDetail = (id: number) => {
    const quotation = quotations.find(q => q.id === id);
    if (quotation) {
      setSelectedQuotation(quotation);
      setDetailDialogOpen(true);
    }
  };

  const handleDownloadPdf = (id: number) => {
    const quotation = quotations.find(q => q.id === id);
    if (quotation) {
      setSelectedQuotation(quotation);
      setPreviewDialogOpen(true);
    }
  };

  const handleSendEmail = (id: number) => {
    const quotation = quotations.find(q => q.id === id);
    if (quotation) {
      setEmailQuotation(quotation);
      setEmailDialogOpen(true);
    }
  };

  const handleEmailSend = (emailData: {
    to: string;
    cc: string;
    subject: string;
    content: string;
  }) => {
    // In a real app, this would call an API to send the email
    console.log('Sending email:', emailData);
    
    // Update quotation status to 'sent' if it was 'draft'
    if (emailQuotation?.status === 'draft') {
      const updatedQuotations = quotations.map(q => 
        q.id === emailQuotation.id 
          ? { ...q, status: 'sent' as const } 
          : q
      );
      setQuotations(updatedQuotations);
    }
    
    // Show success message
    alert(t('pages.quotations.notifications.emailSent'));
    setEmailDialogOpen(false);
  };

  const handleCreateQuotation = () => {
    setCreateDialogOpen(true);
  };

  const handleCloseCreateDialog = () => {
    setCreateDialogOpen(false);
  };

  const handleSaveQuotation = (quotation: Quotation) => {
    const newQuotation = {
      ...quotation,
      id: Math.max(...quotations.map(q => q.id), 0) + 1,
    };
    
    setQuotations([newQuotation, ...quotations]);
    setCreateDialogOpen(false);
  };

  // Filter quotations
  const filteredQuotations = quotations.filter(q => {
    const matchesSearch = q.name.toLowerCase().includes(searchTerm.toLowerCase()) || 
                          q.customer.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCustomer = !customerFilter || q.customer === customerFilter;
    const matchesStatus = statusFilter === 'all' || q.status === statusFilter;
    return matchesSearch && matchesCustomer && matchesStatus;
  });

  const paginatedQuotations = filteredQuotations.slice(
    page * rowsPerPage,
    page * rowsPerPage + rowsPerPage
  );

  // Cc hm tin ch  c chuyn vo component QuotationList

  return (
    <Box sx={{ p: 3 }}>
      {emailQuotation && (
        <EmailQuotationDialog
          open={emailDialogOpen}
          onClose={() => setEmailDialogOpen(false)}
          onSend={handleEmailSend}
          customerName={emailQuotation.customer}
          serviceName={emailQuotation.name}
          responsiblePerson={t('pages.customers.columns.owner')}
          pdfFileName={`BaoGia_${emailQuotation.id}.pdf`}
        />
      )}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3, flexWrap: 'wrap', gap: 2 }}>
        <Typography variant="h4" component="h1">
          {t('pages.quotations.title')}
        </Typography>
        {currentTab === 'quotes' && (
          <Button
            variant="contained"
            color="primary"
            startIcon={<AddIcon />}
            onClick={handleCreateQuotation}
          >
            {t('pages.quotations.actions.createQuotation')}
          </Button>
        )}
      </Box>
      
      {/* Tabs */}
      <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
        <Tabs 
          value={currentTab} 
          onChange={handleTabChange}
          aria-label="quotation management tabs"
        >
          <Tab label={t('pages.quotations.tabs.templates')} value="templates" />
          <Tab label={t('pages.quotations.tabs.quotes')} value="quotes" />
        </Tabs>
      </Box>
      
      {currentTab === 'templates' ? (
        <>
          <QuotationTemplates 
            templates={templates}
            loading={loading}
            onUploadClick={handleOpenUploadDialog}
            onViewTemplate={handleViewTemplate}
            onDeleteTemplate={handleDeleteTemplate}
            onDownloadTemplate={handleDownloadTemplate}
          />
          
          <UploadQuoteTemplate 
            open={uploadDialogOpen}
            onClose={handleCloseUploadDialog}
            onSubmit={handleUploadTemplate}
          />
        </>
      ) : (
        <>
          {/* Search and Filter */}
          <Paper sx={{ p: 2, mb: 3 }}>
            <Stack direction={{ xs: 'column', md: 'row' }} spacing={2} alignItems="center">
              <TextField
                label={t('pages.quotations.searchPlaceholder')}
                variant="outlined"
                size="small"
                value={searchTerm}
                onChange={handleSearch}
                sx={{ minWidth: 200, flex: 1 }}
                InputProps={{
                  startAdornment: <SearchIcon fontSize="small" sx={{ mr: 1, color: 'text.secondary' }} />,
                }}
              />
              
              <Autocomplete
                options={fakeCustomers}
                value={customerFilter}
                onChange={handleCustomerFilterChange}
                renderInput={(params) => <TextField {...params} label={t('pages.quotations.fields.customer')} size="small" />}
                sx={{ minWidth: 200 }}
              />
              
              <FormControl size="small" sx={{ minWidth: 150 }}>
                <InputLabel id="status-filter-label">{t('pages.quotations.filters.status')}</InputLabel>
                <Select
                  labelId="status-filter-label"
                  value={statusFilter}
                  label={t('pages.quotations.filters.status')}
                  onChange={handleStatusFilterChange}
                >
                  <MenuItem value="all">{t('pages.quotations.filters.all')}</MenuItem>
                  {statusOptions.map(option => (
                    <MenuItem key={option.value} value={option.value}>
                      {t(`pages.quotations.status.${option.value}`)}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              
              <Button 
                variant="outlined" 
                startIcon={<ExportIcon />}
                onClick={() => console.log('Export data')}
              >
                {t('pages.quotations.actions.exportExcel')}
              </Button>
            </Stack>
          </Paper>

          {/* Quotation List */}
          <QuotationList
            quotations={paginatedQuotations}
            onViewDetail={handleViewQuotationDetail}
            onDownloadPdf={handleDownloadPdf}
            onSendEmail={handleSendEmail}
            page={page}
            rowsPerPage={rowsPerPage}
            onPageChange={handlePageChange}
            onRowsPerPageChange={handleRowsPerPageChange}
            totalCount={filteredQuotations.length}
          />
        </>
      )}
      
      {/* Create quotation dialog */}
      <QuotationForm
        open={createDialogOpen}
        onClose={handleCloseCreateDialog}
        onSave={handleSaveQuotation}
        customers={fakeCustomers}
        deals={fakeDeals}
        requirements={fakeRequirements}
        templates={templates}
      />

      {/* Dialog xem chi tit bo gi */}
      <QuotationDetail
        open={detailDialogOpen}
        onClose={() => setDetailDialogOpen(false)}
        quotation={selectedQuotation}
      />

      {/* Preview and export PDF dialog */}
      <QuotationPreview
        open={previewDialogOpen}
        onClose={() => setPreviewDialogOpen(false)}
        quotation={selectedQuotation}
        onSaveDraft={(quotation) => {
          // Cp nht bo gi vi ni dung mi
          const updatedQuotations = quotations.map(q => 
            q.id === quotation.id ? quotation : q
          );
          setQuotations(updatedQuotations);
          setPreviewDialogOpen(false);
          alert(t('pages.quotations.notifications.saveDraftSuccess'));
        }}
        onExportPdf={(quotation) => {
          console.log('Export PDF', quotation);
          alert(t('pages.quotations.notifications.exportPdfSuccess'));
        }}
        onEdit={() => {
          setPreviewDialogOpen(false);
          // M li form chnh sa nu cn
        }}
      />
    </Box>
  );
};

export default QuotationManagement;
